<!-- eslint-disable -->
<template>
  <div class="clinica-wrapper movement">
    <div id="clinica">
      <div class="copy-wrapper">
        <h1 class="title main">{{title}}</h1>
        <div class="line"></div>
        <div class="copy" v-html="content"></div>
      </div>
      <div class="ctas">
        <router-link :to="url">
          <div class="btn blue">
            <div class="txt">{{cta}}</div>
          </div>
        </router-link>

        <div class="btn white" @click="openContactForm()">
          <div class="txt">Contacte-nos</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      title: "",
      content: "",
      cta: "",
      url: "",
    };
  },
  methods: {
    offsetTop() {
      var slideshow = $(".slideshow-wrapper");
      var aboutSection = $(".clinica-wrapper");
      aboutSection.css("margin-top", slideshow.height());
    },
    openContactForm() {
      var contact_form = document.querySelector("#contact-form-2");
      var menu = document.querySelector("#menu");

      contact_form.style.transition = ".5s";
      contact_form.style.transform = "translate(-50%, -50%)";
      contact_form.style.pointerEvents = "all";
      $("#contact-form-2 .page").css("pointer-events", "all");
      var html_elem = document.documentElement;
      html_elem.style.overflow = "hidden";

      $(".menu-wrapper-mobile .menu-items").removeClass("open");
      $(".menu-wrapper-mobile .btn-menu").removeClass("open");
      $(".menu-wrapper-mobile .close-menu").removeClass("open");

      $("#contact-form-2 .p0 .text").animate({ left: 0, opacity: 1 }, 500);
    },
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    this.offsetTop();

    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://www.anacarolinapereira.pt/db-api/getAboutHomepage.php")
      .then((resp) => {
        const data = resp.data;

        vm.title = data[0].title;
        vm.content = data[0].content;
        vm.cta = data[0].cta_txt;
        vm.url = data[0].link;
      })
      .then(() => {
        vm.FadeIn();
      });
  },
  created() {
    window.addEventListener("resize", this.offsetTop);
  },
  destroyed() {
    window.removeEventListener("resize", this.offsetTop);
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: rgba(11, 171, 197, 0.6);

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.clinica-wrapper {
  background-color: $white;
  height: pxToEm(400);
  padding: $pd-desktop;
  overflow: hidden;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  #clinica {
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    max-width: pxToEm(1000);

    .copy-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      overflow: hidden;
      // padding-bottom: pxToEm(40);

      .title {
        margin: 0;
        margin-top: pxToEm(50);
        color: $dk-gray-3;
        font-weight: normal;

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-top: pxToEm(40);
        }
      }

      .line {
        width: pxToEm(79);
        height: pxToEm(1);
        background-color: $turquiose;
        margin-top: pxToEm(15);
      }

      .copy {
        font-size: pxToEm(20);
        max-width: pxToEm(1200);
        text-align: center;
        width: 100%;
        color: $dk-gray-3;
        font-family: "OS-Regular";
        margin-top: pxToEm(20);
      }
    }

    .ctas {
      margin: pxToEm(30) 0 pxToEm(50) 0;
      max-width: pxToEm(355);
      justify-content: space-between;
      display: flex;
      width: 100%;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        flex-direction: column;
        align-items: center;
        margin-bottom: pxToEm(40);
      }

      .btn {
        width: pxToEm(161);
        height: pxToEm(44);
        border-radius: pxToEm(10);

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-bottom: pxToEm(20);
        }

        .txt {
          font-family: Arial, Helvetica, sans-serif;
          font-size: pxToEm(17);
          font-weight: bold;
        }
      }
    }
  }
}
</style>



